<template>
  <div class="partner">
    <div class="container">
      <img src="../../assets/partner/banner-cooperation.png" alt="">
    </div>
    <div class="top">
      <img src="../../assets/partner/ic-cooperation.png" alt="">
      <div class="title">{{$t('m.cooperationPartner')}}</div>
      <div>
        <div class="line"></div>
      </div>
      <img src="../../assets/partner/pic-partner.png" alt="" class="partnerimg">
      <div class="state">
        <div class="title">{{$t('m.newState')}}</div>
      </div>
      <div>
        <div class="line"></div>
      </div>
      <div class="carousal">
        <img src="../../assets/partner/ic-switch-1.png" alt="" class="left" @click="prev">
        <div class="item-wrapper">
          <div ref="itemdiv">
            <div class="item" v-for='(item, index) in carousal'>
              <img :src="item.src" alt="">
              <div class="text">{{item.text}}</div>
            </div>
          </div>
        </div>
        <img src="../../assets/partner/ic-switch-2.png" alt="" class="left" @click="next">
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        index: 0,
        carousal: [
          {
            src: require('../../assets/partner/pic-cooperation-news-1.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-2.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-3.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-1.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-2.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-3.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-1.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-2.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          },
          {
            src: require('../../assets/partner/pic-cooperation-news-3.png'),
            text: 'ThaiFintech携手共创移动支付生态圈'
          }
        ]
      }
    },
    created () {
      window.scrollTo(0, 0)
    },
    methods: {
      prev () {
        if (this.index < (this.carousal.length - 3)) {
          this.index ++
          let width = -368 * this.index + 'px'
          let t = `transition:all 0.8s;margin-left:${width}`
          this.$refs.itemdiv.style = t
        }
      },
      next () {
        if (this.index > 0) {
          let width = -368 * this.index + 368 + 'px'
          let t = `transition:all 0.8s ease-out;margin-left:${width}`
          this.$refs.itemdiv.style = t
          this.index --
        }
      }
    }
  }
</script>
<style scoped lang="stylus">
  .partner
    overflow hidden
    .container
      position relative
      height 500px
      img
        position absolute
        left -360px
    .top
      text-align center
      width 1200px
      margin 0 auto
      margin-top 58px
      img
        width 234px
        height 178px
      .title
        margin-top 40px
        margin-bottom 20px
        font-family: MicrosoftYaHei;
        font-size: 32px;
        line-height: 1;
        text-align: center;
        color: #383838;
      .line
        display inline-block
        width: 72px;
        height: 1px;
        border-top: 2px solid #d5d5d5;
      .partnerimg
        width 1034px
        height 466px
        margin-top 14px
        margin-bottom 34px
      .carousal
        text-align left
        margin-bottom 90px
        margin-top 32px
        img
          margin-top 94px
          cursor pointer
        .left
          width 24px
          height 46px
          display inline-block
          vertical-align top
        .item-wrapper
          display inline-block
          width 1104px
          overflow hidden
          padding-bottom 10px
          white-space: nowrap
          .item
            width 324px
            height 240px
            display inline-block
            box-shadow 0 4px 10px #ccc
            margin 0 22px
            img
              width 324px
              height 174px
              margin-top 0px
              cursor auto
            .text
              font-family: MicrosoftYaHei;
              font-size: 16px;
              line-height: 1;
              text-align: left;
              color: #3d3743;
              margin-left 20px
              margin-top 22px


</style>


